.select {
  position: relative;
}

.select__native {
  height: 0;
  opacity: 0;
  position: absolute;
  width: 0;
}

.select__native:focus + .select__input {
  @include outline;
}

.select__input {
  @extend .textInput;
  cursor: pointer;
  padding-right: 50px;

  &:before {
    background-image: url('/img/sprite.png');
    background-position-x: -22px;
    background-position-y: -65px;
    content: '';
    height: 7px;
    position: absolute;
    right: 19px;
    top: 22px;
    width: 12px;
  }
}

.select__input--active {
  background-color: #3d4050;
  border: 0;
  border-radius: 4px 4px 0 0;
  color: #fff;

  &:before {
    background-position-x: -38px;
  }
}

.select__dropDown {
  background-color: rgba(39, 39, 51, 0.9);
  border: 1px solid #3d4050;
  border-top: 0;
  box-sizing: border-box;
  max-height: 0;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 100%;
  transition: max-height 0.25s ease;
  width: 100%;
  z-index: 1;
}

.select__dropDown--active {
  height: auto;
  max-height: 180px;
}

.select__listWrapper {
  max-height: 180px;
}

.select__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.select__item {
  align-items: center;
  color: #fff;
  cursor: pointer;
  display: flex;
  font-size: 18px;
  height: 34px;
  line-height: 1em;
  overflow: hidden;
  padding: 0 15px;
  text-overflow: ellipsis;
  vertical-align: middle;
  white-space: nowrap;
}

.select__item--current,
.select__item:hover {
  color: $highlited;
}
