.order {
  background-color: #272733;
  background-image: url("/img/map.png");
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  min-height: 880px;
  padding-top: 123px;
  padding-bottom: 148px;
}

.order__title {
  color: #fff;
  font-size: 24px;
  letter-spacing: 0.7px;
  line-height: 1em;
  margin-bottom: 22px;
  text-align: center;

  @media (min-width: $tablet-breakpoint) {
    font-size: 36px;
  }
}

.order__description {
  color: #bbb;
  font-size: 16px;
  line-height: 1em;
  margin-bottom: 80px;
  text-align: center;

  @media (min-width: $tablet-breakpoint) {
    font-size: 18px;
  }
}

.order__infographic {
  margin-bottom: 72px;
}

.order__fieldGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (min-width: $tablet-breakpoint) {
    flex-wrap: nowrap;
  }
}

.order__field {
  margin-bottom: 16px;
  width: 100%;

  @media (min-width: $tablet-breakpoint) {
    margin-bottom: 0;
    width: 30%;
    width: calc((100% - 60px) / 3);
  }
}

.order__field--double {
  margin-bottom: 32px;

  @media (min-width: $tablet-breakpoint) {
    margin-bottom: 0;
    width: calc((100% - 60px) / 3 * 2 + 30px);
  }
}

.order__field--submit {
  margin: 19px auto 0;
}

