.footerNav {
  width: 100%;
}

.footerNav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;

  @media (min-width: $desktop-breakpoint) {
    flex-wrap: nowrap;
  }
}

.footerNav__item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

.footerNav__link {
  align-items: center;
  color: #fff;
  display: flex;
  text-decoration: none;
  white-space: nowrap;

  &:hover {
    text-decoration: underline;
  }
}

.footerNav--dark .footerNav__link {
  color: #c3c3c3;
}

.footerNav__icon {
  background-image: url("/img/sprite.png");
  background-position-y: 0;
  display: inline-block;
}

.footerNav__icon--qiwi {
  background-position-x: 0;
  height: 15px;
  margin-right: 9px;
  width: 15px;
}

.footerNav__icon--yandex {
  background-position-x: -19px;
  height: 15px;
  margin-right: 11px;
  width: 15px;
}

.footerNav__icon--webmoney {
  background-position-x: -38px;
  height: 15px;
  margin-right: 11px;
  width: 11px;
}

.footerNav__icon--email {
  background-position-x: -53px;
  height: 13px;
  margin-right: 10px;
  width: 15px;
}

.footerNav__icon--vk {
  background-position-x: -72px;
  height: 13px;
  margin-right: 8px;
  width: 11px;
}

