.footer {
  background-color: #3d4050;
  box-sizing: border-box;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  justify-content: center;
  padding: 32px 0 29px;
};

.footer__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  width: 100%;

  @media (min-width: $desktop-breakpoint) {
    justify-content: space-between;
  }
}

.footer__item {
  box-sizing: border-box;
  flex-grow: 1;
  margin-bottom: 20px;
  padding: 0;
  position: relative;
  text-align: center;
  width: 100%;

  @media (min-width: $tablet-breakpoint) {
    padding: 0 20px;
  }

  @media (min-width: $desktop-breakpoint) {
    margin-bottom: 0;
    padding: 0 40px;
    text-align: left;
    width: auto;
  }

  &:before {
    background-color: #c3c3c3;
    content: '';
    display: none;
    height: 18px;
    position: absolute;
    right: 0;
    width: 1px;

    @media (min-width: $desktop-breakpoint) {
      display: block;
    }
  }

  &:first-child {
    padding-left: 0;
  }

  &:last-child {
    padding-right: 0;

    &:before {
      display: none;
    }
  }
}

.footer__wallets {
  width: 377px;
}

.footer__contacts {
  width: 286px;
}
