.textInput {
  background-color: #dfdfe1;
  border: 1px solid #fff;
  border-radius: 4px;
  box-sizing: border-box;
  color: #272733;
  font-size: 18px;
  font-weight: 400;
  height: 48px;
  line-height: 46px;
  padding: 0 15px;
  width: 100%;
  transition: transform 0.25s ease, color 0.25s ease, border-color 0.25s ease;

  &::placeholder {
    color: #272733;
  }
}
