.br {
  display: none;
}

.br--mobile {
  @media (max-width: $tablet-breakpoint - 1) {
    display: block;
  }
}

.br--tablet {
  @media (min-width: $tablet-breakpoint) and (max-width: $desktop-breakpoint - 1) {
    display: block;
  }
}

.br--desktop {
  @media (min-width: $desktop-breakpoint) {
    display: block;
  }
}
