.infographicItem {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.infographicItem__iconWrapper {
  align-items: center;
  background-color: #3e9cdc;
  border: 5px solid #286690;
  border-radius: 50px;
  box-sizing: border-box;
  display: flex;
  height: 100px;
  justify-content: center;
  margin-bottom: 16px;
  width: 100px;
}

.infographicItem__icon {
  background-image: url("/img/sprite.png");
  background-position-y: -19px;
  height: 35px;
  width: 34px;
}

.infographicItem__icon--loupe {
  background-position-x: 0;
  height: 35px;
  width: 34px;
}

.infographicItem__icon--percent {
  background-position-x: -38px;
  height: 32px;
  width: 32px;
}

.infographicItem__icon--plus {
  background-position-x: -74px;
  height: 42px;
  width: 37px;
}

.infographicItem__icon--envelope {
  background-position-x: -115px;
  height: 29px;
  width: 36px;
}

.infographicItem__icon--bond {
  background-position-x: -154px;
  height: 26px;
  width: 39px;
}

.infographicItem__text {
  color: #fff;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
}
