.nav {
  align-items: center;
  display: flex;
  justify-content: flex-start;
}

.nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;

  @media (min-width: $tablet-breakpoint) {
    flex-direction: row;
    width: auto;
  }
}

.nav__item {
  @media (min-width: $tablet-breakpoint) {
    margin-right: 58px;
  }
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #fff;
  display: inline-block;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.3px;
  line-height: 50px;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
}

.nav__link--active,
.nav__link:hover {
  color: $highlited;
}
