.header {
  background-color: rgba(16, 16, 29, 0.9);
  box-sizing: border-box;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;

  @media (min-width: $desktop-breakpoint) {
    padding: 25px 0;
  }
};

.header__container {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__burger {
  margin: 18px 0;
  transform: translateX(24px);

  @media (min-width: $desktop-breakpoint) {
    display: none;
  }
}

.header__nav {
  display: none;
  flex-direction: column;
  justify-content: center;
  width: 100%;

  @media (min-width: $desktop-breakpoint) {
    display: flex;
    width: auto;
  }
}
