.section {
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
}

.section__container {
  box-sizing: border-box;
  padding-left: $gutter;
  padding-right: $gutter;

  @media screen and (min-width: $desktop-breakpoint) {
    margin: 0 auto;
    max-width: $desktop-container-max-width;
    padding-left: 0;
    padding-right: 0;
  }
}
