.scroll {
  align-items: stretch;
  display: flex;
  justify-content: space-between;
}

.scroll__wrapper {
  overflow: hidden;
  width: 100%;
}

.scroll__content {
  padding: 2px 0;
}

.scroll__line {
  background-color: #3d4050;
  border-radius: 3px;
  margin: 3px;
  width: 7px;
}

.scroll__handler {
  background-color: $highlited;
  border-radius: inherit;
  cursor: pointer;
  min-height: 30px;
  position: relative;
  width: 100%;

  &:before {
    bottom: -20px;
    content: '';
    left: -20px;
    position: absolute;
    right: 20px;
    top: -20px;
  }
}
