.infographic {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0 auto;
  padding: 0;

  @media (min-width: $tablet-breakpoint) {
    justify-content: space-around;
    max-width: 700px;
  }

  @media (min-width: $desktop-breakpoint) {
    justify-content: space-between;
    max-width: 100%;
  }
}

.infographic__item {
  margin-bottom: 40px;
  width: 50%;

  @media (min-width: $tablet-breakpoint) {
    width: 30%;
  }

  @media (min-width: $desktop-breakpoint) {
    margin-bottom: 0;
    width: auto;
  }
}

.infographic__item--mediate {
  display: none;
  width: 0;

  @media (min-width: $desktop-breakpoint) {
    display: block;
  }
}

.infographic__item--mediate1 {
  transform: translatex(12px);
}

.infographic__item--mediate2 {
  transform: translatex(-1px);
}

.infographic__item--mediate3 {
  transform: translatex(-8px);
}

.infographic__item--mediate4 {
  transform: translatex(10px);
}

.infographic__mediate {
  display: flex;
  height: 6px;
  justify-content: space-between;
  position: absolute;
  transform: translate(-52px, 46px);
  width: 102px;
}
