.slider {
  align-items: stretch;
  display: flex;
  flex-wrap: wrap;
  height: 48px;
}

.slider__header {
  align-items: flex-start;
  color: #fff;
  display: flex;
  font-size: 18px;
  justify-content: space-between;
  line-height: 1em;
  width: 100%;
}

.slider__title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transform: translateY(-4px);
}

.slider__percent {
  font-weight: 600;
  letter-spacing: 1px;
  padding-left: 30px;
  transform: translateY(-2px);
}

.slider__input {
  height: 0;
  opacity: 0;
  width: 0;
}

.slider__input:focus + .slider__line {
  @include outline;

  .slider__handler {
    @include outline;
  }
}

.slider__line {
  align-self: flex-end;
  background-color: #bfbfc2;
  border: 1px solid #fff;
  border-radius: 3px;
  box-sizing: border-box;
  height: 6px;
  width: 100%;
}

.slider__handlerPath {
  height: 0;
  margin: 3px 10px;
  position: relative;
}

.slider__handler {
  background-color: #42a9ed;
  border: 3px solid #286690;
  border-radius: 12px;
  box-sizing: border-box;
  cursor: pointer;
  height: 24px;
  left: 100%;
  position: absolute;
  transform: translate(-12px, -12px);
  width: 24px;

  &:before {
    content: '';
    left: -20px;
    position: absolute;
    top: -20px;
    bottom: -20px;
    right: -20px;
  }
}
