.showcase {
  align-items: center;
  background-color: #f3f8fc;
  background-image: url("/img/bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #2a2b30;
  display: flex;
  font-size: 16px;
  height: 98vh;
  line-height: 24px;
  max-height: 849px;
  min-height: 700px;
  padding-top: 100px;

  @media (min-width: $tablet-breakpoint) {
    font-size: 18px;
    line-height: 28px;
  }

  @media (min-width: $desktop-breakpoint) {
    font-size: 20px;
    line-height: 34px;
  }
}

.showcase__container {
  padding-top: 20px;
  padding-bottom: 60px;
  width: 100%;
}

.showcase__title {
  color: #272733;
  font-size: 28px;
  font-weight: 600;
  line-height: 1.33;
  margin-bottom: 20px;

  @media (min-width: $tablet-breakpoint) {
    font-size: 32px;
    margin-bottom: 32px;
  }

  @media (min-width: $desktop-breakpoint) {
    font-size: 48px;
    margin-bottom: 51px;
  }
}

.showcase__paragraph {
  font-weight: 600;
  letter-spacing: 0.05px;
}

.showcase__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.showcase__item {
  padding-left: 12px;
  position: relative;

  &:before {
    content: '-';
    left: 0;
    position: absolute;
    top: 1px;
  }
}

.showcase__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 32px;


  @media (min-width: $tablet-breakpoint) {
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-top: 40px;
    width: 393px;
  }

  @media (min-width: $desktop-breakpoint) {
    margin-top: 71px;
  }
}

.showcase__order,
.showcase__more {
  width: 100%;

  @media (min-width: $tablet-breakpoint) {
    width: auto;
  }
}

.showcase__order {
  margin-bottom: 16px;

  @media (min-width: $tablet-breakpoint) {
    margin-bottom: 0;
    padding-left: 59px;
    padding-right: 56px;
  }
}

.showcase__more {
  @media (min-width: $tablet-breakpoint) {
    padding-left: 49px;
    padding-right: 47px;
  }
}
